/* Slider */
.slick-slider {
  position: relative;
  display: block;
  box-sizing: border-box;
  -moz-box-sizing: border-box;
  -webkit-touch-callout: none;
  -webkit-user-select: none;
  -khtml-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
  -ms-touch-action: pan-y;
  touch-action: pan-y;
  -webkit-tap-highlight-color: transparent;
}

.slick-list {
  position: relative;
  overflow: hidden;
  display: block;
  margin: 0;
  padding: 0;
}

.slick-list:focus {
  outline: none;
}

.slick-list.dragging {
  cursor: pointer;
  cursor: hand;
}

.slick-slider .slick-track,
.slick-slider .slick-list {
  -webkit-transform: translate3d(0, 0, 0);
  -moz-transform: translate3d(0, 0, 0);
  -ms-transform: translate3d(0, 0, 0);
  -o-transform: translate3d(0, 0, 0);
  transform: translate3d(0, 0, 0);
}

.slick-track {
  position: relative;
  left: 0;
  top: 0;
  display: block;
}

.slick-track:before, .slick-track:after {
  content: "";
  display: table;
}

.slick-track:after {
  clear: both;
}

.slick-loading .slick-track {
  visibility: hidden;
}

.slick-slide {
  float: left;
  height: 100%;
  min-height: 1px;
  display: none;
}

[dir="rtl"] .slick-slide {
  float: right;
}

.slick-slide img {
  display: block;
}

.slick-slide.slick-loading img {
  display: none;
}

.slick-slide.dragging img {
  pointer-events: none;
}

.slick-initialized .slick-slide {
  display: block;
}

.slick-loading .slick-slide {
  visibility: hidden;
}

.slick-vertical .slick-slide {
  display: block;
  height: auto;
  border: 1px solid transparent;
}

html, body, div, span, applet, object, iframe, h1, h2, h3, h4, h5,
h6, p, blockquote, pre, a, abbr, acronym, address, big,
cite, code, del, dfn, em, img, ins, kbd, q, s, samp, small,
strike, strong, sub, sup, tt, var, b, u, i, center, dl, dt,
dd, ol, ul, li, fieldset, form, label, legend, table, caption,
tbody, tfoot, thead, tr, th, td, article, aside, canvas, details,
embed, figure, figcaption, footer, header, hgroup, menu, nav,
output, ruby, section, summary, time, mark, audio, video {
  margin: 0;
  padding: 0;
  border: 0;
  vertical-align: baseline;
}

html {
  -ms-text-size-adjust: 100%;
  -webkit-text-size-adjust: 100%;
}

img, fieldset, a img {
  border: none;
}

input[type="text"],
input[type="email"],
input[type="tel"],
textarea {
  -webkit-appearance: none;
}

input[type="submit"],
button {
  cursor: pointer;
}

input[type="submit"]::-moz-focus-inner,
button::-moz-focus-inner {
  padding: 0;
  border: 0;
}

textarea {
  overflow: auto;
}

input, button {
  margin: 0;
  padding: 0;
  border: 0;
}

div, input, textarea, select, button,
h1, h2, h3, h4, h5, h6, a, span, a:focus {
  outline: none;
}

ul, ol {
  list-style-type: none;
}

table {
  border-spacing: 0;
  border-collapse: collapse;
  width: 100%;
}

html {
  box-sizing: border-box;
}

*, *:before, *:after {
  box-sizing: inherit;
}

@media (prefers-reduced-motion: reduce) {
  * {
    transition: none !important;
  }
}

a {
  text-decoration: none;
}

img, figure {
  display: block;
}

video {
  width: 100%;
}

@font-face {
  font-family: "Bebas";
  src: url(../fonts/bebas.eot);
  src: url(../fonts/bebas.woff);
  src: url(../fonts/bebas.ttf);
  font-style: normal;
  font-weight: 400;
}

@font-face {
  font-family: "HelveticaNeue";
  src: url(../fonts/HelveticaNeue.eot);
  src: url(../fonts/HelveticaNeue.woff);
  src: url(../fonts/HelveticaNeue.ttf);
  font-style: normal;
  font-weight: 400;
}

@font-face {
  font-family: "HelveticaNeue-LT";
  src: url(../fonts/HelveticaNeueLTStd-Hv.eot);
  src: url(../fonts/HelveticaNeueLTStd-Hv.woff);
  src: url(../fonts/HelveticaNeueLTStd-Hv.ttf);
  font-style: normal;
  font-weight: 400;
}

.container {
  max-width: 940px;
  margin: 0 auto;
}

@media only screen and (max-width: 960px) {
  .container {
    padding: 0 20px;
  }
}

.header__content {
  padding: 22px 0;
  display: flex;
  justify-content: space-between;
  align-items: center;
}

@media only screen and (max-width: 570px) {
  .nav {
    position: absolute;
    background: rgba(232, 96, 60, 0.9);
    top: 150px;
    bottom: 0;
    z-index: 2;
    left: -100%;
    width: 0;
  }
  .nav--is-visible {
    position: absolute;
    background: rgba(232, 96, 60, 0.9);
    top: 150px;
    bottom: 0;
    z-index: 2;
    position: fixed;
    left: 0;
    right: 0;
    width: 100%;
  }
}

.nav__link {
  padding-left: 23px;
  font-family: "Bebas";
  font-weight: 400;
  font-size: 16px;
  text-transform: uppercase;
  color: #b9b9b9;
  text-decoration: none;
}

.nav__link:hover {
  color: #e8603c;
}

@media only screen and (max-width: 570px) {
  .nav__link {
    display: block;
    padding: 10px 23px;
    color: #ffffff;
    font-size: 20px;
  }
  .nav__link:hover, .nav__link:active {
    color: #ffffff;
  }
}

.nav__link--is-active {
  color: #e8603c;
}

.nav-button {
  display: none;
}

@media only screen and (max-width: 570px) {
  .nav-button {
    display: flex;
    flex-direction: column;
    cursor: pointer;
  }
}

.nav-button__line {
  width: 35px;
  height: 4px;
  border-radius: 4px;
  margin-bottom: 5px;
  background: #e8603c;
}

.nav-button__line-center {
  transform: translate(0px);
  transition: transform 1s;
}

.nav-button__line-center--close {
  transform: translate(8px);
  transition: transform 1s;
}

.headline {
  background: rgba(232, 96, 60, 0.5);
}

@media only screen and (max-width: 1000px) {
  .headline {
    background: #e8603c;
  }
}

.headline__text {
  position: relative;
  padding: 14px 16px;
  background: #e8603c;
  font-family: "Bebas";
  font-weight: 400;
  font-size: 24px;
  color: #ffffff;
}

.headline__text:before {
  content: '';
  display: block;
  position: absolute;
  background: #ffffff;
  width: 10px;
  height: 70px;
  left: 100%;
  top: 0;
}

.headline__text:after {
  content: '';
  display: block;
  position: absolute;
  background: #ffffff;
  width: 10px;
  height: 70px;
  right: 100%;
  top: 0;
}

@media only screen and (max-width: 1000px) {
  .headline__text {
    padding-left: 0;
  }
  .headline__text:before, .headline__text:after {
    display: none;
  }
}

.body-overflow {
  overflow: hidden;
}

.main {
  padding: 26px 0;
}

.main__container {
  display: flex;
  justify-content: space-between;
  align-items: flex-start;
}

@media only screen and (max-width: 660px) {
  .main__container {
    flex-direction: column;
  }
}

.post-list {
  width: 71.2%;
}

@media only screen and (max-width: 660px) {
  .post-list {
    order: 2;
    width: 100%;
  }
}

.post {
  position: relative;
}

.post:after {
  content: '';
  display: block;
  background: url("../img/bg/line.png");
  width: 100%;
  height: 14px;
  margin: 7% 0 8%;
}

.post__icon {
  position: absolute;
  top: 0;
  right: 100%;
  margin-right: 10px;
  background: #e8603c;
}

@media only screen and (max-width: 1130px) {
  .post__icon {
    display: none;
  }
}

.post__icon--slider {
  padding: 22px 24px 20px 23px;
}

.post__icon--img {
  padding: 22px 23px 20px;
}

.post__icon--video {
  padding: 22px 22px 21px 26px;
}

.post__icon--quote {
  padding: 14px 20px 17px 17px;
}

.post__icon--url {
  padding: 17px 24px 17px 19px;
}

.post__media {
  position: relative;
}

.post__img {
  width: 100%;
  overflow: hidden;
}

.post__article {
  display: flex;
  justify-content: flex-start;
  padding-top: 20px;
}

@media only screen and (max-width: 400px) {
  .post__article {
    flex-wrap: wrap;
  }
}

.post__meta {
  width: 146px;
  font-size: 12px;
  line-height: 20px;
}

@media only screen and (max-width: 400px) {
  .post__meta {
    width: 100%;
    order: 2;
    padding-top: 4%;
  }
}

.post__description {
  width: 76%;
}

@media only screen and (max-width: 400px) {
  .post__description {
    width: 100%;
  }
}

.post__title {
  font-family: "Bebas";
  font-weight: 400;
  color: #e8603c;
  font-size: 18px;
  line-height: 18px;
  padding-bottom: 4.2%;
}

.post__text {
  font-family: "HelveticaNeue";
  font-weight: 400;
  font-size: 12px;
  line-height: 18px;
  color: #414141;
}

.post__text--more {
  padding-top: 3.5%;
}

.post__text--quote {
  background: #f5f5f5;
  font-size: 13px;
  font-style: italic;
  color: #474747;
  padding: 23px 30px 18px 76px;
  border: none;
}

.post__text--quote:before {
  content: ',,';
  transform: rotate(180deg);
  display: block;
  text-align: center;
  font-family: Georgia;
  font-style: normal;
  font-weight: 400;
  line-height: 0;
  letter-spacing: -5px;
  width: 40px;
  height: 40px;
  position: absolute;
  top: 20px;
  left: 30px;
  font-size: 72px;
  color: #e8663c;
}

.post__text--quote:focus {
  outline-color: #e8603c;
}

@media only screen and (max-width: 400px) {
  .data {
    display: inline-block;
  }
}

.data__caption {
  font-family: "Bebas";
  font-weight: 400;
  color: #e8603c;
}

@media only screen and (max-width: 400px) {
  .data__caption {
    display: inline-block;
    padding: 0 6px;
    font-size: 10px;
  }
}

.data__description {
  padding: 3px 0 6px;
  font-family: "HelveticaNeue";
  font-weight: 400;
  color: #bababa;
}

@media only screen and (max-width: 400px) {
  .data__description {
    display: inline-block;
  }
}

.data__tags {
  display: inline-block;
}

.data__tags--before:before {
  content: "|";
  display: inline-block;
  padding: 0 6px;
  position: relative;
  bottom: 1px;
}

.slider__img {
  width: 100%;
}

.slider__arrow {
  position: absolute;
  top: 50%;
  width: 1.7%;
  padding: 1.7%;
  transform: rotate(45deg) translate(-50%);
  border: 13px solid #ffffff;
  opacity: 0.9;
  cursor: pointer;
}

@media only screen and (max-width: 500px) {
  .slider__arrow {
    border-width: 5px;
  }
}

.slider__arrow--prev {
  left: 6%;
  border-right: none;
  border-top: none;
}

.slider__arrow--next {
  right: 2%;
  border-left: none;
  border-bottom: none;
}

.video {
  position: relative;
  width: 100%;
}

.video__inner {
  padding-top: 56.25%;
  height: 0;
}

.video__content {
  position: absolute;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
}

.post__url {
  font-family: "HelveticaNeue";
  font-weight: 400;
  font-size: 12px;
  line-height: 18px;
  color: #a0a0a0;
  margin-bottom: 10px;
}

.post__url:hover {
  text-decoration: underline;
}

.post__text--url {
  padding-top: 2.7%;
}

.post__title--url {
  padding-bottom: 6px;
}

.pagination__item {
  display: inline-block;
  width: 24px;
  height: 26px;
  text-align: center;
  margin-left: 3px;
}

.pagination__link {
  font-family: "Bebas";
  font-weight: 400;
  display: block;
  padding: 2px 0 5px;
  background: #f3b29d;
  color: #ffffff;
  font-size: 14px;
  line-height: 18px;
}

.pagination__link--is-active, .pagination__link:hover {
  background: #e8663c;
}

.post__is-hidden {
  display: none;
  font-family: "HelveticaNeue";
  font-weight: 400;
  font-size: 12px;
  color: #414141;
}

.post__comments {
  margin: 10px 0;
  border-top: 1px solid #e8603c;
}

.post__input {
  border: 1px solid #e8603c;
  border-radius: 4px;
  width: 100%;
  padding: 5px;
  margin: 5px 0;
}

.post__form-button {
  background: #e8603c;
  color: #ffffff;
  font-size: 15px;
  padding: 10px 40px;
  border-radius: 10px;
}

@media only screen and (max-width: 470px) {
  .post__form-button {
    padding: 8px 10px;
    font-size: 13px;
  }
}

.comment__inner {
  display: flex;
  justify-content: flex-start;
  background: #f5f5f5;
  border-radius: 4px;
  padding: 5px;
  margin-top: 15px;
}

.comment__username {
  font-size: 14px;
  color: #e8603c;
}

.comment__date {
  padding-top: 5px;
  color: #bababa;
}

.comment__text {
  margin-left: 20px;
}

.comment__error {
  display: inline-block;
  color: #e8603c;
  padding-left: 20px;
}

.aside {
  width: 220px;
  position: sticky;
  top: 3%;
  font-family: "HelveticaNeue";
  font-weight: 400;
  font-size: 12px;
  color: #464646;
}

@media only screen and (max-width: 860px) {
  .aside {
    margin-left: 10px;
  }
}

@media only screen and (max-width: 660px) {
  .aside {
    width: 100%;
    position: static;
    margin-left: 0px;
  }
}

.small__title {
  padding-bottom: 7.8%;
  display: flex;
  align-items: center;
  font-family: "Bebas";
  font-weight: 400;
  font-size: 14px;
  text-transform: uppercase;
  color: #e8603c;
  white-space: nowrap;
}

@media only screen and (max-width: 660px) {
  .small__title {
    display: block;
    padding-bottom: 2%;
  }
}

.small__title:after {
  content: '';
  display: block;
  background: url("../img/bg/line.png");
  width: 100%;
  height: 14px;
  margin-left: 17px;
  margin-top: 2px;
}

@media only screen and (max-width: 660px) {
  .small__title:after {
    display: none;
  }
}

.additional__content {
  padding-bottom: 16%;
}

@media only screen and (max-width: 660px) {
  .additional__content {
    padding-bottom: 4%;
  }
}

.categories__link {
  color: #464646;
}

.categories__link:hover {
  text-decoration: underline;
}

.categories__count {
  display: inline-block;
}

@media only screen and (max-width: 660px) {
  .categories {
    text-align: right;
    display: inline-block;
    width: 49%;
  }
}

@media only screen and (max-width: 380px) {
  .categories {
    width: 100%;
    text-align: left;
  }
}

.gallery__block {
  display: flex;
  justify-content: space-between;
  flex-wrap: wrap;
}

.gallery__photo {
  width: 50px;
  height: 50px;
  margin-bottom: 5px;
  background: #f3af9d;
  border: 1px solid #e8603c;
}

@media only screen and (max-width: 660px) {
  .gallery__photo {
    margin: 3px;
  }
}

.gallery__img {
  width: 100%;
  overflow: hidden;
}

@media only screen and (max-width: 660px) {
  .gallery:after {
    content: '';
    display: block;
    background: url("../img/bg/line.png");
    width: 100%;
    height: 14px;
    margin: 7% 0 1%;
  }
}

@media only screen and (max-width: 660px) {
  .about {
    display: inline-block;
    width: 49%;
    text-align: left;
    vertical-align: top;
  }
}

@media only screen and (max-width: 380px) {
  .about {
    width: 100%;
  }
}
